import { useNavigate } from "react-router-dom";
import { useCallback } from "react";

import { TPage } from "../constants";

type TGoTo = (page: TPage) => void;

type TUseGoToPage = () => TGoTo;

const useGoToPage: TUseGoToPage = () => {
    const navigate = useNavigate();

    const goTo: TGoTo = useCallback((page) => {
        navigate(page);
    }, [
        navigate,
    ]);

    return goTo;
};

export default useGoToPage;
