import { useCallback } from "react";

import { useBeep as useBeepUi } from '@brz/ui';

import getErrorMsg from "../utils/getErrorMsg";

type TBeep = {
    beepError: (error: unknown) => void;
    beepInfo: (message: string) => void;
};

type TUse = () => TBeep

const useBeep: TUse = () => {
    const beep = useBeepUi();

    const beepError = useCallback((error: unknown) => {
        beep.error(getErrorMsg(error));
    }, [
        beep,
    ]);

    const beepInfo = useCallback((message: string) => {
        beep.info(message);
    }, [
        beep,
    ]);


    return {
        beepError,
        beepInfo,
    };
};

export default useBeep;
