import { useCallback } from 'react';

import { TImg } from '../state/draw/types/types';

import * as drawActions from '../state/draw/actions';

import useAppDispatch from './useAppDispatch';

const useDrawInit = () => {
    const dispatch = useAppDispatch();

    const drawInit = useCallback((img: TImg) => {
        dispatch(drawActions.init(img));
    }, [
        dispatch,
    ]);

    return drawInit;
};

export default useDrawInit;
