import React, { useCallback } from 'react';

import {
    imageElementToDataURL,
    useIsMounted,
    useSpin,
} from '@brz/ui';

import LayoutMain from '../../componeents/LayoutMain';

import screen1 from './screen1.png';
import screen2 from './screen2.png';
import screen3 from './screen3.png';

import useDrawInit from '../../hooks/useDrawInit';
import useGoToPage from '../../hooks/useGoToPage';
import useBeep from '../../hooks/useBeep';

import './style.css';

const Demo = () => {
    const { beepError } = useBeep();
    const goToPage = useGoToPage();
    const drawInit = useDrawInit();
    const isMounted = useIsMounted();

    const [loading, setLoading] = useSpin(false);

    const onClick = useCallback(async (event: React.MouseEvent<HTMLImageElement, MouseEvent>) => {
        setLoading(true);
        try {
            const imageElement = event.currentTarget;
            const dataUrl = await imageElementToDataURL(imageElement);
            const width = imageElement.naturalWidth;
            const height = imageElement.naturalHeight;

            if (isMounted()) {
                drawInit({
                    dataUrl,
                    width,
                    height,
                });
                goToPage('/draw');
            }
        } catch (error) {
            beepError(error);
        }
        setLoading(false);
    }, [
        drawInit,
        goToPage,
        isMounted,
        setLoading,
        beepError,
    ]);

    return (
        <LayoutMain loading={loading}>
            <div className="Demo">
                <img
                    className="Demo__img"
                    src={screen1}
                    alt="screen1"
                    onClick={onClick}
                />
                <img
                    className="Demo__img"
                    src={screen2}
                    alt="screen1"
                    onClick={onClick}
                />
                <img
                    className="Demo__img"
                    src={screen3}
                    alt="screen1"
                    onClick={onClick}
                />
            </div>
        </LayoutMain>
    );
};

export default Demo;
