import { createSelector } from "@reduxjs/toolkit";

import figureFocusedIdSelector from "./figureFocusedId";
import figuresSelector from "./figures";

const figureFocused = createSelector(figureFocusedIdSelector, figuresSelector, (figureFocusedId, figures) => {
    if (figureFocusedId !== null) {
        const figure = figures.find(x => x.id === figureFocusedId);
        if (figure) {
            return figure;
        }
    }
    return null;
});

export default figureFocused;
