import React from 'react';

import { APP_REPO_LINK } from '../../config';

import NavLink from "./NavLink";

import * as drawSelectors from '../../state/draw/selectors';

import useAppSelector from '../../hooks/useAppSelector';

import styles from './style.module.css';

const Header = () => {
    const img = useAppSelector(drawSelectors.img);

    return (
        <div className={styles.Header}>
            <NavLink
                to="/"
                text="Home"
            />

            <NavLink
                to="/demo"
                text="Demo"
            />

            {!!img && (
                <NavLink
                    to="/draw"
                    text="Draw"
                />
            )}

            <a
                className={styles.NavLink}
                href={APP_REPO_LINK}
                target="_blank"
                rel="noopener noreferrer"
            >
                GitHub
            </a>
        </div>
    );
};

export default Header;
